import axios, { AxiosInstance } from 'axios';

// ===== TIPOS OFICIAIS AUDESP =====

export interface AudeAuthRequest {
  email: string;
  password: string;
}

export interface AudeAuthResponse {
  token: string;
  expire_in: number;
  token_type: 'bearer';
}

export interface AudeProtocolo {
  protocolo: string;
  data_hora: string;
  status: 'Recebido' | 'Processado' | 'Rejeitado';
  tipo: string;
  mensagens?: string[];
  campos_invalidos?: string[];
}

export interface AudeEnvioResponse {
  protocolo: string;
  timestamp: string;
  mensagens: string[];
}

export interface AudeValidacaoError {
  campo: string;
  mensagem: string;
  codigo: string;
}

// ===== CLIENTE AUDESP =====

class AudeClient {
  private client: AxiosInstance;
  private token: string = '';
  private expireAt: number = 0;

  constructor(
    private baseUrl: string = 'https://sistemas.tce.sp.gov.br/audesp/api',
    private apiKey: string = ''
  ) {
    this.client = axios.create({
      baseURL: this.baseUrl,
      timeout: 30000,
      headers: {
        'Content-Type': 'application/json',
        'X-API-Key': this.apiKey
      }
    });
  }

  // ===== AUTENTICAO =====

  async login(email: string, password: string): Promise<AudeAuthResponse> {
    try {
      const response = await this.client.post<AudeAuthResponse>(
        '/login',
        { email, password },
        {
          headers: {
            'x-authorization': `${email}:${password}`
          }
        }
      );

      this.token = response.data.token;
      this.expireAt = Date.now() + (response.data.expire_in * 1000);

      // Atualizar header padro
      this.client.defaults.headers.common['Authorization'] = `Bearer ${this.token}`;

      return response.data;
    } catch (error) {
      throw new Error(`Falha na autenticao AUDESP: ${error}`);
    }
  }

  isAuthenticated(): boolean {
    return this.token !== '' && Date.now() < this.expireAt;
  }

  getToken(): string {
    return this.token;
  }

  // ===== FASE IV: LICITAES E CONTRATOS =====

  async enviarEdital(
    editalJson: any,
    pdfFile?: File
  ): Promise<AudeEnvioResponse> {
    const formData = new FormData();
    formData.append('edital', JSON.stringify(editalJson));

    if (pdfFile) {
      formData.append('pdf', pdfFile);
    }

    return this.enviar('/recepcao-fase-4/f4/enviar-edital', formData);
  }

  async enviarLicitacao(licitacaoJson: any): Promise<AudeEnvioResponse> {
    return this.enviarJson('/recepcao-fase-4/f4/enviar-licitacao', licitacaoJson);
  }

  async enviarAta(ataJson: any): Promise<AudeEnvioResponse> {
    return this.enviarJson('/recepcao-fase-4/f4/enviar-ata', ataJson);
  }

  async enviarAjuste(ajusteJson: any): Promise<AudeEnvioResponse> {
    return this.enviarJson('/recepcao-fase-4/f4/enviar-ajuste', ajusteJson);
  }

  // ===== FASE V: PRESTAO DE CONTAS =====

  async enviarPrestacaoContasConvenio(
    prestacaoJson: any
  ): Promise<AudeEnvioResponse> {
    return this.enviarJson('/f5/enviar-prestacao-contas-convenio', prestacaoJson);
  }

  async enviarPrestacaoContasContratoGestao(
    prestacaoJson: any
  ): Promise<AudeEnvioResponse> {
    return this.enviarJson('/f5/enviar-prestacao-contas-contrato-gestao', prestacaoJson);
  }

  async enviarPrestacaoContasTermoColaboracao(
    prestacaoJson: any
  ): Promise<AudeEnvioResponse> {
    return this.enviarJson('/f5/enviar-prestacao-contas-termo-colaboracao', prestacaoJson);
  }

  async enviarPrestacaoContasTermoFomento(
    prestacaoJson: any
  ): Promise<AudeEnvioResponse> {
    return this.enviarJson('/f5/enviar-prestacao-contas-termo-fomento', prestacaoJson);
  }

  async enviarPrestacaoContasTermoParceria(
    prestacaoJson: any
  ): Promise<AudeEnvioResponse> {
    return this.enviarJson('/f5/enviar-prestacao-contas-termo-parceria', prestacaoJson);
  }

  async enviarDeclaracaoNegativa(
    declaracaoJson: any
  ): Promise<AudeEnvioResponse> {
    return this.enviarJson('/f5/declaracao-negativa', declaracaoJson);
  }

  // ===== CONSULTA DE PROTOCOLOS =====

  async consultarProtocoloF4(protocolo: string): Promise<AudeProtocolo> {
    return this.consultar(`/f4/consulta/${protocolo}`);
  }

  async consultarProtocoloF5(protocolo: string): Promise<AudeProtocolo> {
    return this.consultar(`/f5/consulta/${protocolo}`);
  }

  // ===== MTODOS AUXILIARES PRIVADOS =====

  private async enviarJson(
    endpoint: string,
    data: any
  ): Promise<AudeEnvioResponse> {
    return this.enviar(endpoint, data);
  }

  private async enviar(
    endpoint: string,
    data: any
  ): Promise<AudeEnvioResponse> {
    try {
      if (!this.isAuthenticated()) {
        throw new Error('Sesso expirada. Faa login novamente.');
      }

      const response = await this.client.post<AudeEnvioResponse>(
        endpoint,
        data,
        {
          headers: data instanceof FormData
            ? { 'Content-Type': 'multipart/form-data' }
            : { 'Content-Type': 'application/json' }
        }
      );

      return response.data;
    } catch (error: any) {
      const mensagem = error.response?.data?.mensagem || error.message;
      const camposInvalidos = error.response?.data?.campos_invalidos || [];

      throw new Error(JSON.stringify({
        mensagem,
        camposInvalidos,
        statusCode: error.response?.status
      }));
    }
  }

  private async consultar(endpoint: string): Promise<AudeProtocolo> {
    try {
      if (!this.isAuthenticated()) {
        throw new Error('Sesso expirada. Faa login novamente.');
      }

      const response = await this.client.get<AudeProtocolo>(endpoint);
      return response.data;
    } catch (error: any) {
      throw new Error(`Erro ao consultar protocolo: ${error.message}`);
    }
  }
}

export default AudeClient;
